@import "@/scss/settings.scss";
























































































































































.item {
	position: relative;
   width: 500px;
   height: 500px;
	max-width: 100%;
   cursor: pointer;
   display: inline-flex;
   justify-content: center;
   align-items: center;
   opacity: 0;

   &__title{
      z-index: 99;
      position: absolute;
      font-size: 2rem;
      text-transform: uppercase;
      color: white;
      top: 50%;
      left:50%;
      transform: translate(-50%,-50%);
      flex-direction: row;
      display: flex;
      & > span {
         opacity: 0;
      }
   }
   &__deco{
      fill: rgba(70,70,70,0.6);
   }
   &__svg{
      position: relative;
      display: block;
      width: 80%;
      height: 80%;
      margin: 0 auto;
      color: white;
      //border: 1px solid yellow;
   }
   &__clippath,
   &__deco,
   &__img {
      transform-origin: 50% 50%;
   }
   &__meta {
      position: absolute;
      top: 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
   }
   &__number {
      position: absolute;
      top: 0;
      width: 100%;
      padding: 12.5% 0 0 3em;
      letter-spacing: 0.5em;
      text-transform: uppercase;
   }
   &__specimen {
      font-size: 5em;
      font-weight: 500;
      line-height: 0.9;
      vertical-align: text-bottom;
      letter-spacing: -0.025em;
   }
   &__reference {
      font-size: 1.25em;
      -webkit-writing-mode: vertical-rl;
      writing-mode: vertical-rl;
   }
   &__subtitle {
      font-size: 1em;
      font-weight: 500;
      margin: 0.5em 0 0;
      text-align: center;
      letter-spacing: 0.15em;
      opacity: 0;
   }
}
