@import "@/scss/settings.scss";













































.item__deco{
    fill: rgba(70,70,70,0.6);
    transform-origin: center center;
    transform: scale(.7);
}
.item__outline{
    &__1{
        transform-origin: center center;
        transform: scale(.8);
        fill: transparent;
        stroke: rgba(70,70,70,0.6);
        stroke-width: 2;
    }
    &__2{
        transform-origin: center center;
        transform: scale(.9);
        fill: transparent;
        stroke: rgba(70,70,70,0.5);
    }
}
